@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto:wght@300&display=swap);
:root {
  /*Main colors*/
  --background-light: #eee;
  --background: white;
  --background-dark: #F8F8F8;

  --accent1: #EEEEEE;

  /*Nav*/
  --main-active: var(--accent1-dark);
  --accent-active: var(--accent1-dark);

  /*Various Text*/
  --text: #444444;
  --note: gray;
  --important: red;
  --button-text: black;

  /*CONST*/
  --nav-size: 50px;
  --footer-size: 50px;
}

* {
  box-sizing: border-box;
}

html,
body {
  display: -webkit-flex;
  display: flex;
  overflow-x: hidden;
}

body,
#root {
  height: calc(100vh - 50px - 50px);
  height: calc(100vh - var(--nav-size) - var(--footer-size));
  width: 100%;
}

body {
  background-color: white;
  background-color: var(--background);
  color: #444444;
  color: var(--text);

  margin: 0;
  margin-top: 50px;
  margin-top: var(--nav-size);
  padding: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  line-height: 1.1;
}

a {
  color: gray;
  color: var(--note);
  transition: color 0.25s;
}

a:hover {
  color: var(--accent1-dark);
}
